import { Component, OnInit, Injector, ViewEncapsulation, ViewChild } from '@angular/core';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { SessionService } from 'src/app/core/session/session.service';
import { Subscription } from 'rxjs';
import { MatDrawer } from '@angular/material/sidenav';

import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-visitor-setup',
  templateUrl: './visitor-setup.component.html',
  styleUrls: ['./visitor-setup.component.scss'],
  encapsulation : ViewEncapsulation.None
})
export class VisitorSetupComponent implements OnInit {
  	@ViewChild('matDrawer', {static: true}) matDrawer: MatDrawer;
  	visitor:any = {};
	clickMode:string;
	isDataLoaded:boolean = false;
	isDrawerLoader:boolean = true;
	visitorCategoryList = [];
	categorySearch:string;
	totalItems:number;
	modalService:any;
	apiSubscibe:Subscription;

 	constructor(
		private lookupService: LookupService,
		private injector: Injector,
		private sharedService:SharedService,
		private sessionService:SessionService
	) {this.modalService = this.injector.get(ModalService)}

	addVisitorCategory() {
		this.clickMode = 'add';
		this.matDrawer.open();
		this.visitor = {};
	}

	editVisitorCategory(item) {
		this.clickMode = 'edit';
		this.visitor =  {...item};
		window.scroll({
			top: 0,
			behavior: 'smooth'
		});
		this.matDrawer.open();
	}

	deleteVisitorCategory(item, index){
    	this.closeDrawer();
		let params = {
			id:item.lookupValueId,
			index:index
		}
		this.modalService.showConfirmModal(params);
 	}
  
	closeDrawer() {
		this.matDrawer.close();
		this.clickMode = '';
		this.visitor = {};
	}

	submitVisitorCategoryForm() {
    	this.isDrawerLoader = false;
		if(this.clickMode == 'add') {
			let params = {
				lookupvalue: {
					"apartmentId" :  this.sessionService.apartmentId,
					"lookupTypeId": 100, // Visitor category lookup id
					"lookupValueName": this.visitor.lookupValueName,
					"description": this.visitor.description,
					"isActive": true,
					"insertedBy":parseInt(this.sessionService.userId),
					"insertedOn":_moment().toISOString(),
					"updatedBy": null,
					"updatedOn":null
				}
			}
			this.lookupService.addLookupValue(params).subscribe((res:any) => {
				this.isDrawerLoader = true;
				if(res.code == 200) {
					this.closeDrawer();
					this.sharedService.openSnackBar('Visitor Type Created Successfully', 'success');
					this.getVisitorList();
				} else {
					this.sharedService.openSnackBar(res.responseData.value.errorMessage,'error');
				}
     		},
			(error)=>{
				this.isDrawerLoader = true;
				this.sharedService.openSnackBar('Network Error', 'error');
			});
		} else {
			let params = {
				lookupvalue: {
					"apartmentId" :  this.sessionService.apartmentId,
					"lookupValueId" : this.visitor.lookupValueId,
					"lookupTypeId": 100, // Visitor category lookup id
					"lookupValueName": this.visitor.lookupValueName,
					"description": this.visitor.description,
					"isActive": true,
					"insertedBy": this.visitor.insertedBy,
					"insertedOn": this.visitor.insertedOn,
					"updatedBy": parseInt(this.sessionService.userId),
					"updatedOn":_moment().toISOString()
				}
			}
			this.lookupService.updateLookupValue(params).subscribe((res:any) => {
				this.isDrawerLoader = true;
				if(res.message) {
					this.closeDrawer();
					this.getVisitorList();
					this.sharedService.openSnackBar(res.message,'success');
				} else {
					this.sharedService.openSnackBar(res.errorMessage,'error');
				}
      		},
			(error)=>{
				this.isDrawerLoader = true;
				this.sharedService.openSnackBar('Network Error', 'error');
			});
		}
	}

	getVisitorList() {
    	this.isDataLoaded = false;
		let params = {
			LookupTypeId: 100,
			ApartmentId: this.sessionService.apartmentId,
		}
		this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {
			this.isDataLoaded = true;
			this.visitorCategoryList = res;
      		this.totalItems = res.length;
		});
	}

	ngOnInit() {
		this.getVisitorList();
		//delete item
		this.apiSubscibe = this.sharedService.unitlistdeleteindexcast.subscribe( item => {
			if(item != null) {
				this.isDataLoaded = false;
				var params = {
					lookupValueId: item.id,
					updateUserId: parseInt(this.sessionService.userId)
				}
				this.lookupService.deleteLookupvalue(params).subscribe((res:any) => {
					this.sharedService.setUnitListDeleteIndex(null);
					this.isDataLoaded = true;
					if(res.message) {
						this.visitorCategoryList.splice(item.index, 1);
						this.totalItems -= 1;
						this.clickMode = '';   //close input box
						this.sharedService.openSnackBar(res.message,'success');
					} else {
						this.sharedService.openSnackBar(res.errorMessage,'error');
					}
				});
			}
		});
	}

	ngOnDestroy(){
		this.apiSubscibe.unsubscribe();
	}

}

