import { Component, OnInit, ChangeDetectorRef, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { StaffService } from 'src/app/api/controllers/Staff';
import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SearchCountryField, CountryISO } from 'ngx-intl-tel-input';

import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-create-vendor-frquent-visitor',
  templateUrl: './create-vendor-frquent-visitor.component.html',
  styleUrls: ['./create-vendor-frquent-visitor.component.scss']
})
export class CreateVendorFrquentVisitorComponent implements OnInit {
  @ViewChild('expectedVisitorForm') form:any;
  visitor:any = {};
  urlType:string;
  message: any = null;
  isDataLoaded:boolean = true;
  visitTypeList:any =[];
  visitCategoryList:any = [];
  towerList:any = [];
  unitList:any = [];
  staffsList:any = [];
  block:any = {};
  type:string;


  separateDialCode = true;
  SearchCountryField = SearchCountryField;
  CountryISO = CountryISO;
  preferredCountries: CountryISO[] = [CountryISO.UnitedStates, CountryISO.UnitedKingdom];

  constructor(
    private apartmentService:ApartmentService,
    private visitorService:VisitorService,
    private lookupService: LookupService,
    private sessionService:SessionService,
    private staffService: StaffService,
    private router:Router,
    private activateRouter: ActivatedRoute,
    private sharedService: SharedService,
    private _changeDetectorRef: ChangeDetectorRef,
  ) {
    this.activateRouter.url.subscribe((data)=>{
      this.urlType = data[0].path;
      if(this.urlType == 'vendor-pass-create')
      this.type = 'Vendor';
      else if(this.urlType == 'frequent-visitor-create')
      this.type = 'Frequent';
    })
  }

  get isAdmin() {
    return this.sessionService.isAdmin();
  }

  resetField() {
    this.visitor={};
    this.visitor.visitTypeId = null;
    this.visitor.visitorCategoryId = null;
    this.block = {
      blockId:null,
      primaryName:''
    }
  }

  setSelectedCategory(event){
    this.visitor.visitorCategoryId = event[0].lookupValueId
  }

  setSelectedVisitType(event){
    this.visitor.visitTypeId = event[0].lookupValueId;
    this.visitor.apartmentUnitId = null;
    this.visitor.tomeetStaffId = null;
    this.block = {
      blockId:null,
      primaryName:''
    }
  }

  setSelectedBlock(event) {
    this.block.blockId = event[0].block_Id
    this.getUnits('change');
  }

  setSelectedBlockUnit(event){
    this.visitor.apartmentUnitId = event[0].buId
    this.getPrimaryName();
  }

  setSelectedStaff(event){
    this.visitor.tomeetStaffId = event[0].staffId;
    this.visitor.apartmentUnitId = null;
    this.block = {
      blockId:null,
      primaryName:''
    }
  }

  getTowers() {
    let tower = {
     apartmentId: this.sessionService.apartmentId
    }
    return this.apartmentService.getApartmentBlockAndBlockUnitIsLivingByApartmentId(tower).toPromise();
  }

  getUnits(type) {
    if(type == 'change') {
      this.visitor.apartmentUnitId = null;
      this.block.primaryName = '';
    }
    for(let data of this.towerList) {
      if(this.block.blockId == data.block_Id) {
        this.unitList = data.blockUnit;
        break;
      }
    }
  }

  getPrimaryName() {
    for(let data of this.unitList) {
      if(this.visitor.apartmentUnitId == data.buId) {
        this.block.primaryName = data.il_Label;
        break;
      }
    }
  }

  back() {
    if(this.urlType == 'vendor-pass-create')
      this.router.navigate(['vendor-pass-list'],{relativeTo: this.activateRouter.parent});
    else if(this.urlType == 'frequent-visitor-create')
      this.router.navigate(['frequent-visitor-list'],{relativeTo: this.activateRouter.parent});
  }

  createExpectedVisitor() {
    this.message = null;
    if(!this.form.valid){
      window.scroll({
        top: 0,
        behavior: 'smooth'
      });
      // Show the validation message
      this.message = {
        appearance: 'outline',
        content   : "Fill the Required Fields",
        shake     : true,
        showIcon  : true,
        type      : 'error'
      };
      //Mark for check
      this._changeDetectorRef.markForCheck();
    } else {
      this.isDataLoaded = false;
      let params = {
        expectedVisitor : {
          ...this.visitor,
          "apartmentUnitId": this.visitor.apartmentUnitId,
          "apartmentId" : this.sessionService.apartmentId,
          "expectedVisitorPhone": this.visitor.expectedVisitorPhone.number,
          "expectedVisitorInTime": this.visitor.expectedVisitorInTime ? moment(this.visitor.expectedVisitorInTime).format('HH:mm') : '',
          "expectedVisitorOutTime": this.visitor.expectedVisitorOutTime ? moment(this.visitor.expectedVisitorInTime).format('HH:mm') : '',
          "isActive": true,
          "insertedBy": this.sessionService.userId,
          "insertedOn": _moment().toISOString(),
          "updatedBy": null,
          "updatedOn": null,
          "enteredBy": parseInt(this.sessionService.userId),
          "meetingPersonId": 1,
          "visitorsOrg": "string",
          "entryGateId": null,
          "exitGateId": null,
          "tomeetStaffId": this.visitor.tomeetStaffId,
          "phonecountrycode":  this.visitor.expectedVisitorPhone.countryCode,
          "isDailyvendor":  this.urlType == 'vendor-pass-create' ? true : false,
          "isFrequentvisitor": this.urlType == 'frequent-visitor-create' ? true : false,
        }
      }
      this.visitorService.addExpectedVisitor(params).subscribe((res:any) => {
        this.isDataLoaded = true;
        if(res.message) {
          let id,type;
          id = res.message.split('#');
          if(this.urlType == 'vendor-pass-create')
            type = 'vendor';
          else if(this.urlType == 'frequent-visitor-create')
            type = 'frequent';
          this.router.navigate(['confirm-expected-visitor', id[0], type],{relativeTo: this.activateRouter.parent});
        } else {
          this.sharedService.openSnackBar(res.errorMessage,'error');
        }
      },(error)=>{
        this.sharedService.openSnackBar('Server Error','error');
      })
    }
  }

  ngOnInit(): void {
    this.resetField();
    this.getTowers().then((res:any)=>{
      if(res.length > 0) {
        this.towerList = res;
      }
    })

    //set apartmentBlockUnitId and VisitorTypeId in User Mode
     if(!this.isAdmin) {
      this.visitor.apartmentUnitId = this.sessionService.apartmentBlockUnitID;
      this.visitor.visitTypeId = 50 // unitVisitor Type
    }

    //visit type
    let visitParams = {
      ApartmentId: this.sessionService.apartmentId,
      LookupTypeId: 15
    }
    this.lookupService.getLookupValueByLookupTypeId(visitParams).subscribe((res:any) => {
      this.visitTypeList = res;
    });

    //Visit Category
    let visitCategoryParams = {
      ApartmentId: this.sessionService.apartmentId,
      LookupTypeId: this.urlType == 'frequent-visitor-create' ? 202 : 100
    }
    this.lookupService.getLookupValueByLookupTypeId(visitCategoryParams).subscribe((res:any) => {
      this.visitCategoryList = res
    });

    //Staff
    let staffParms = {
      apartmentId: this.sessionService.apartmentId,
      RoleTypeId: this.sessionService.roleTypeId
    }
    this.staffService.getAllStaffs(staffParms).subscribe((res:any) => {
      this.staffsList = res;;
    });
  }

}
