import { Component, OnInit, Injector, HostListener, ViewChild, ViewEncapsulation } from '@angular/core';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { ModalService } from 'src/app/shared/services/modal.service';
import { ConfirmDialogModel, CommonConfirmModalComponent } from 'src/app/shared/components/common-confirm-modal/common-confirm-modal.component';
import { MatDialog } from '@angular/material/dialog';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { Router, ActivatedRoute } from '@angular/router';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';
import { MatDrawer } from '@angular/material/sidenav';

import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-exp-visitor-list',
  templateUrl: './exp-visitor-list.component.html',
  styleUrls: ['./exp-visitor-list.component.scss'],
  encapsulation: ViewEncapsulation.None
})

export class ExpVisitorListComponent implements OnInit {
  
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent; 
  @ViewChild('matDrawer', {static: true}) matDrawer: MatDrawer;
  columnData:any;
  visitorList:any = [];
  visitorSearch:string = '';
  isVisitorDataLoaded:boolean;
  modalService:any;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 10;
  
  filterField:any = {
    fromDate: moment(new Date()).subtract(30, 'days').format(),
    toDate: _moment().toISOString(),
  }

  timeZone:any;

  constructor(
    private router: Router,
    private injector: Injector,
    private visitorService:VisitorService,
    private sharedService:SharedService,
    private sessionService:SessionService,
    private activateRouter:ActivatedRoute,
    public dialog: MatDialog,
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  navigateToCreate(){
    this.router.navigate(['/ams/visitor/info/create-expected-visitor']);
  }

  getPrintParams(event) {
    this.datagrid.exportdata(event, 'expectedVisitorData');
  }

  getDate(date) {
    return moment(date).format(this.timeZone.date)
  }

  getDateTime(date) {
    return moment(date).format(this.timeZone.time)
  }

  checkIn(item) {

    const message = `Are you sure you want to Check In?`;
    const dialogData = new ConfirmDialogModel("Confirm Action", message);
    const dialogRef = this.dialog.open(CommonConfirmModalComponent, {
      panelClass: 'material-dialog-medium',
      disableClose: true,
      data: dialogData
    });
    dialogRef.afterClosed().subscribe(dialogResult => {
      if (dialogResult) {
        this.router.navigate([`checkin/${item.expectedVisitorId}`],{relativeTo:this.activateRouter.parent,queryParams : {access : 'expected'}});
      }
    });

  }
  

  @HostListener('window:onCopyWith', ['$event.detail'])
  onCopyWith(detail) {
    const message = `Are you sure, you want to copy the details?`;
    const dialogData = new ConfirmDialogModel("Confirm Action", message);
    const dialogRef = this.dialog.open(CommonConfirmModalComponent, {
      panelClass: 'material-dialog-medium',
      disableClose: true,
      data: dialogData
    });
    dialogRef.afterClosed().subscribe(dialogResult => {
      if (dialogResult) {
        let dataRecord = this.datagrid.getrowdata(detail.rowId);
        this.router.navigate([`/ams/visitor/info/expected-visitor/copy/${dataRecord.expectedVisitorId}`]);
      }
    });
  }

  filterApply() {
    this.getVisitorList();
    this.matDrawer.close();
    this.filterField.fromDate = moment(this.filterField.fromDate).utc().format();
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  goBack() {
    this.matDrawer.close();
  }

  clearFilter() {
    this.filterField = {
      fromDate: moment(new Date()).subtract(1, 'days').format(),
      toDate: _moment().toISOString(),
    }
    this.getVisitorList();
  }

  getVisitorList() {
    
    this.isVisitorDataLoaded = false;
    
    let params = {
      apartmentId: this.sessionService.apartmentId,
      fromDate : moment(this.filterField.fromDate).toISOString(),
      toDate: moment(this.filterField.toDate).toISOString(),
    }
    
    this.visitorService.getAllYetToComeExpectedVisitorsByApartmentIdDate(params).subscribe((res:any) => {
      if(res.length > 0) {
        
        /*let tableData = {
          localdata: res.reverse(),
          datatype: "array"
        }
        this.totalItems = tableData.localdata.length;
        this.visitorList = new jqx.dataAdapter(tableData); */

        this.visitorList = res.reverse();
        this.totalItems = this.visitorList.length;

        if (this.totalItems > this.itemLimit) {
          this.ItemEndIndex = this.itemLimit;
        }
        else {
          this.ItemEndIndex = this.totalItems;
        }

        this.isVisitorDataLoaded = true;

      }
      this.isVisitorDataLoaded = true;
    },(error)=>{
      this.isVisitorDataLoaded = true;
      this.sharedService.openSnackBar('Server Error','error');
    })

  }

  ngOnInit() {

    this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )

    this.getVisitorList();

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.columnData = [{
      text: 'Id',
      datafield: 'serialNo',
      width: 80,
      pinned: true,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },{
      text: 'Visitor Name',
      datafield: 'expectedVisitorName',
      width: 140,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },{
      text: 'Phone Number',
      datafield: 'expectedVisitorPhone',
      cellsrenderer: cellsrenderer,
      minwidth: 100,
      renderer: columnrenderer
    },{
      text: 'Expected Date/Time of Visit',
      datafield: 'expectedVisitorInTime',
      cellsrenderer: (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">'+ moment(value).format("MM/DD/YY hh:mm") +'</div>'
      },
      minwidth: 160,
      renderer: columnrenderer
    },{
      text: 'Expected Time-out',
      datafield: 'expectedVisitorOutTime',
      cellsrenderer: (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">'+ moment(value).format("MM/DD/YY hh:mm") +'</div>'
      },
      minwidth: 160,
      renderer: columnrenderer
    },{
      text: 'Visit Type',
      datafield: 'visitType_Label',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Visitor category',
      datafield: 'visitorCategoryId_Label',
      cellsrenderer: cellsrenderer,
      minwidth: 150,
      renderer: columnrenderer
    }, {
      text: 'Tower Unit',
      datafield: 'block_Unit',
      cellsrenderer: cellsrenderer,
      minwidth: 170,
      renderer: columnrenderer 
    }, {
      text: 'Check-In',
      cellsalign: 'center', 
      align: 'center',
      width: 120,
      cellsrenderer: (row: number): string => {
        // let new_ = moment().format('DD/MM/YYYY');
        // let date = moment(this.visitorList.loadedData[row].expectedVisitorInTime).format('DD/MM/YYYY');
        // if(new_ == date) {
          return '<div class="simple-actions">'
          +'<a href="javascript:void(0)" onClick="checkIn('+ row +')" ><img src="assets/images/checkin-icon.svg" width="20" /> </a>'
          +'</div>'
        //}
      },
      renderer: columnrenderer
    },{
      text: 'Copy',
      cellsalign: 'center', 
      align: 'center',
      width: 120,
      cellsrenderer: (row: number): string => {
        let new_ = moment().format('DD/MM/YYYY');
        let date = moment(this.visitorList.loadedData[row].expectedVisitorInTime).format('DD/MM/YYYY');
        if(new_ != date) {
          return '<div class="simple-actions">'
          +'<a href="javascript:void(0)" onClick="copyWith('+ row +')" >Copy Details</a>'
          +'</div>'
        }
      },
      renderer: columnrenderer
    }];
  }
}
function checkIn(row) {
  var event = new CustomEvent('onCheckIn', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).checkIn = checkIn;

function copyWith(row){
  var event = new CustomEvent('onCopyWith', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).copyWith = copyWith;
