import { Component, OnInit, ViewChild, ViewEncapsulation, ChangeDetectionStrategy, ChangeDetectorRef} from '@angular/core';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { SessionService } from 'src/app/core/session/session.service';
import * as moment from 'moment';
import _moment from "moment-timezone";
import { StaffService } from 'src/app/api/controllers/Staff';
import { ActivatedRoute, Router } from '@angular/router';
import { SearchCountryField, CountryISO } from 'ngx-intl-tel-input';
import { CondoAnimations } from 'src/@condo/animations';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-visitor-create-checkin',
  templateUrl: './visitor-create-checkin.component.html',
  styleUrls: ['./visitor-create-checkin.component.scss'],
  encapsulation  : ViewEncapsulation.None,
  animations   : CondoAnimations
})
export class VisitorCreateCheckinComponent implements OnInit { 
  @ViewChild('visitorForm') form:any;
  visitor:any = {};
  expectedDurationInfo:string = '';
  visitTypeList:any = [];
  visitCategoryList:any = [];
  towerList:any = [];
  unitList:any = [];
  staffsList:any = [];
  block:any = {};
  isDataLoaded:boolean = true;
  urlType:string = 'create';
  separateDialCode = true;
  SearchCountryField = SearchCountryField;
  CountryISO = CountryISO;
  preferredCountries: CountryISO[] = [CountryISO.UnitedStates, CountryISO.UnitedKingdom];
  message: any = null;
  pageComingFrom:string;
  constructor(
    private apartmentService:ApartmentService,
    private visitorService:VisitorService,
    private lookupService: LookupService,
    private sessionService:SessionService,
    private staffService: StaffService,
    private activateRouter: ActivatedRoute,
    private router: Router,
    private sharedService: SharedService,
    private _changeDetectorRef: ChangeDetectorRef,
  ) {
    //check in and checkout edit
    this.activateRouter.params.subscribe((param)=>{
      if(param.id && param.type) {
        this.isDataLoaded = false;
        this.urlType = param.type;
        let id = parseInt(param.id);
        if(param.type == 'checkin')
          this.checkInLogic(id);
        else if(param.type == 'checkout' || param.type == 'edit') 
          this.checkoutLogic(id);
      }
    })
    // Data Coming From
    this.activateRouter.queryParams.subscribe((query=>{
      if(query && query.access) {
        this.pageComingFrom = query.access;
      }
    }))
  }

  checkInLogic(id) {
    let visitor = {
      expectedVisitorId : id
    }
    this.visitorService.getExpectedVisitorById(visitor).subscribe((res: any) => {
      this.visitor.visitTypeId = res[0].visitTypeId;
      this.visitor.apartmentUnitId = res[0].apartmentUnitId;
      this.visitor.tomeetStaffId = res[0].tomeetStaffId;
      this.visitor.visitorName = res[0].expectedVisitorName;
      this.visitor.visitorCount = res[0].expectedVisitorCount;
      this.visitor.visitorPhone = {
        'number' : res[0].expectedVisitorPhone,
        'countryCode' : res[0].phonecountrycode,
      }
      this.visitor.visitorInTime = moment(new Date());
      this.visitor.visitorOutTime = res[0].expectedVisitorOutTime;
      this.visitor.expectedDuration = res[0].expectedDuration;
      this.visitor.expectedVisitorInTime = res[0].expectedVisitorInTime;
      this.visitor.expectedVisitorOutTime = res[0].expectedVisitorOutTime;
      this.visitor.passNumber = res[0].visitorPassId;
      this.visitor.visitorPassId = res[0].visitorPassId;
      this.visitor.visitorCategoryId = res[0].visitorCategoryId;
      this.visitor.purpose = res[0].purpose;
      this.visitor.expectedVisitorId = id;
      this.expectedDurationChange();
      if(this.visitor.apartmentUnitId) {
        this.getTowers().then((res:any)=>{
          if(res.length > 0) {
            this.towerList = res;
            this.getUnits('edit');
          }
        })
      } else {
        this.isDataLoaded = true;
      }
    },(error)=>{
      this.sharedService.openSnackBar('Server Error','error');
    })
  }

  checkoutLogic(id) {
    let visitor = {
      visitorId : id
    }
    this.visitorService.getVisitorById(visitor).subscribe((res: any) => {
      this.visitor = res[0];
      this.visitor.visitorPhone = {
        'countryCode' : this.visitor.phonecountrycode,
        'number' : this.visitor.visitorPhone
      }
      if(this.urlType == 'checkout')
        this.visitor.visitorOutTime =  moment(new Date()).toISOString();
      this.expectedDurationChange();
      if(this.visitor.apartmentUnitId) {
        this.getTowers().then((res:any)=>{
          if(res.length > 0) {
            this.towerList = res;
            this.getUnits('edit');
          }
        })
      } else {
        this.isDataLoaded = true;
      }
    },(error)=>{
      this.isDataLoaded = true;
      this.sharedService.openSnackBar('Server Error','error');
      this.router.navigate(['checkout'],{relativeTo:this.activateRouter.parent})
    })
  }

  navigateBack() {
    if(this.pageComingFrom == 'frequent') 
      this.router.navigate(['frequent-visitor-list'],{ relativeTo: this.activateRouter.parent });
    else if(this.pageComingFrom == 'vendor')
      this.router.navigate(['vendor-pass-list'],{ relativeTo: this.activateRouter.parent });
    else if(this.pageComingFrom == 'expected')
      this.router.navigate(['expected-visitor-list'],{ relativeTo: this.activateRouter.parent });
    else if(this.urlType =='checkout'|| this.urlType=='edit')
      this.router.navigate(['checkout'],{ relativeTo: this.activateRouter.parent });
  }

  disableField() {
    if(this.urlType=='checkout' || this.urlType == 'edit')
      return true;
    else 
      return false;
  }

  setSelectedCategory(event){
    this.visitor.visitorCategoryId = event[0].lookupValueId
  }

  setSelectedVisitType(event){
    this.visitor.visitTypeId = event[0].lookupValueId;
    this.visitor.apartmentUnitId = null;
    this.visitor.tomeetStaffId = null;
    this.block = {
      blockId:null,
      primaryName:''
    }
  }

  setSelectedBlock(event) {
    this.block.blockId = event[0].block_Id
    this.getUnits('change');
  }

  setSelectedBlockUnit(event){
    this.visitor.apartmentUnitId = event[0].buId
    this.getPrimaryName();
  }

  setSelectedStaff(event){
    this.visitor.tomeetStaffId = event[0].staffId;
    this.visitor.apartmentUnitId = null;
    this.block = {
      blockId:null,
      primaryName:''
    }
  }

  getParticularBlockId() {
    let unitDetails = {
      apartmentBlockUnitId: this.visitor.apartmentUnitId,
    };
    return this.apartmentService.getApartmentBlockUnitById(unitDetails);
  }

  getTowers() {
     let tower = {
      apartmentId: this.sessionService.apartmentId
    }
    return this.apartmentService.getApartmentBlockAndBlockUnitIsLivingByApartmentId(tower).toPromise();
  }

  async getUnits(type) {
    //Async await function integrated for checkin and checkout mode
    if(type == 'change') {
      this.visitor.apartmentUnitId = null;
      this.block.primaryName = '';
    } else {
      //checkin or checkout mode
      let tower:any = await this.getParticularBlockId().toPromise();
      if(tower.length > 0)
        this.block.blockId = tower[0].apartmentBlockId;
    }
    for(let data of this.towerList) {
      if(this.block.blockId == data.block_Id) {
        this.unitList = data.blockUnit;
        if(type != 'change'){
          this.getPrimaryName();
          this.isDataLoaded = true;
        }
        break;
      }
    }
  }

  getPrimaryName() {
    for(let data of this.unitList) {
      if(this.visitor.apartmentUnitId == data.buId) {
        this.block.primaryName = data.il_Label;
        break;
      }
    }
  }

  expectedDurationChange() {
    if(this.visitor.visitorInTime && this.visitor.expectedVisitorOutTime) {
      let inDate = moment(this.visitor.visitorInTime);
      let outDate = moment(this.visitor.expectedVisitorOutTime);
      let diffDuration = moment.duration(outDate.diff(inDate));
      this.expectedDurationInfo = diffDuration.days()+" days "+this.isSingleDigit(diffDuration.hours())+":"+ this.isSingleDigit(diffDuration.minutes())+" hours";
      let hours = diffDuration.asHours();
      this.visitor.expectedDuration = Math.round(hours);
    }
  }

  isSingleDigit(digit){
    if(digit <= 0){
      return '00';
    } else {
      return (digit.toString().length > 1) ? digit : '0'+digit;
    }
  }

  resetField() {
    this.visitor={};
    this.visitor.visitorInTime = moment(new Date());
    this.visitor.visitTypeId = null;
    this.visitor.visitorCategoryId = null;
    this.block = {
      blockId:null,
      primaryName:''
    }
    this.expectedDurationInfo='';
  }

  createCheckIn() {
    this.message = null;
    if(!this.form.valid){
      window.scroll({
        top: 0,
        behavior: 'smooth'
      });
      // Show the validation message
      this.message = {
        appearance: 'outline',
        content   : "Fill the Required Fields",
        shake     : true,
        showIcon  : true,
        type      : 'error'
      };
      //Mark for check
      this._changeDetectorRef.markForCheck();
    } else {
      this.isDataLoaded = false;
      let params = {
        visitor : {
          ...this.visitor,
          "apartmentId" : this.sessionService.apartmentId,
          "visitorPhone": this.visitor.visitorPhone.number,
          "phonecountrycode": this.visitor.visitorPhone.countryCode,
          "isCheckedIn" : true,
          "isCheckedOut": false,
          "isActive": true,
          "insertedBy": this.sessionService.userId,
          "insertedOn": _moment().toISOString(),
          "updatedBy": null,
          "updatedOn": null,
          "expectedVisitorId": this.visitor.expectedVisitorId,
          "expectedVisitorInTime": this.visitor.expectedVisitorInTime,
          "expectedVisitorOutTime": this.visitor.expectedVisitorOutTime,
          "expectedVisitorDuration": null,
  
          "enteredBy": parseInt(this.sessionService.userId),
          "visitorVehicleTypeId": null,
          "visitorVehicleNumber": "string",
          "visitingPlaceId": null,
          "meetingPersonId": 1,
          "meetingPersonName": "string",
          "visitorsOrg": "string",
          "entryGateId": null,
          "exitGateId": null,
        }
      }
      this.visitorService.addVisitor(params).subscribe((res:any) => {
        this.isDataLoaded = true;
        if(res.message) {
          this.sharedService.openSnackBar('Visitor Checked IN','success');
          this.router.navigate(['checkout'],{ relativeTo: this.activateRouter.parent });
        } else {
          this.sharedService.openSnackBar(res.errorMessage,'error');
        }
      })
    }
  }

  updateCheckIn() {
    this.message = null;
    if(!this.form.valid){
      window.scroll({
        top: 0,
        behavior: 'smooth'
      });
      // Show the validation message
      this.message = {
        appearance: 'outline',
        content   : "Fill the Required Fields",
        shake     : true,
        showIcon  : true,
        type      : 'error'
      };
      //Mark for check
      this._changeDetectorRef.markForCheck();
    } else {
      this.isDataLoaded = false;
      let params = {
        visitor : {...this.visitor,
          "visitorPhone": this.visitor.visitorPhone.number,
          "phonecountrycode": this.visitor.visitorPhone.countryCode,
        }
      }
      this.visitorService.updateVisitor(params).subscribe((res:any) => {
        this.isDataLoaded = true;
        if(res.message) {
          this.sharedService.openSnackBar(res.message,'success');
          this.router.navigate(['checkout'],{ relativeTo: this.activateRouter.parent });
        } else {
          this.sharedService.openSnackBar(res.errorMessage,'error');
        }
      })
    }
  }

  createCheckOut() {
    this.isDataLoaded = true;
    let visitorDetails = {
      "visitorId": this.visitor.visitorId,
      "checkOutTime": this.visitor.visitorOutTime,
      "updatedBy": this.sessionService.userId,
    }
    this.visitorService.checkOutVisitor(visitorDetails).subscribe((res: any) => {
      if(res.message) {
        this.sharedService.openSnackBar(res.message,'success');
        this.router.navigate(['history'],{ relativeTo: this.activateRouter.parent });
      } else {
        this.sharedService.openSnackBar(res.errorMessage,'error');
      }
      this.isDataLoaded = false;
    })
  }

  ngOnInit() {
    //visit type
    let visitParams = {
      ApartmentId: this.sessionService.apartmentId,
      LookupTypeId: 15
    }
    this.lookupService.getLookupValueByLookupTypeId(visitParams).subscribe((res:any) => {
      this.visitTypeList = res;
    });
    //TowerList
    if(this.urlType == 'create') {
      this.resetField();
      this.getTowers().then((res:any)=>{
        if(res.length > 0) {
          this.towerList = res;
        }
      })
    }
    // Staffs List
    let staffParms = {
      apartmentId: this.sessionService.apartmentId,
      RoleTypeId: this.sessionService.roleTypeId
    }
    this.staffService.getAllStaffs(staffParms).subscribe((res:any) => {
      this.staffsList = res;;
    });
    //Visit Category
    let visitCategoryParams = {
      ApartmentId: this.sessionService.apartmentId,
      LookupTypeId: this.pageComingFrom == 'frequent' ? 202 : 100
    }
    this.lookupService.getLookupValueByLookupTypeId(visitCategoryParams).subscribe((res:any) => {
      this.visitCategoryList = res
    });
  }
}