import { Component, OnInit, ViewEncapsulation, ViewChild} from '@angular/core';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { SessionService } from 'src/app/core/session/session.service';
import { StaffService } from 'src/app/api/controllers/Staff';
import { Router, ActivatedRoute } from '@angular/router';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SearchCountryField, CountryISO } from 'ngx-intl-tel-input';
import { CondoAnimations } from 'src/@condo/animations';

import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-exp-visitor-create-chekin',
  templateUrl: './exp-visitor-create-chekin.component.html',
  styleUrls: ['./exp-visitor-create-chekin.component.scss'],
  encapsulation  : ViewEncapsulation.None,
  animations   : CondoAnimations
})

export class ExpVisitorCreateChekinComponent implements OnInit {
  
  @ViewChild('expectedVisitorForm') form:any;
  visitor:any = {};
  expectedDurationInfo:string = '';
  visitTypeList:any = [];
  visitCategoryList:any = [];
  towerList:any = [];
  unitList:any = [];
  staffsList:any = [];
  block:any = {};
  isDataLoaded:boolean = true;
  urlType:string = 'create';
  separateDialCode = true;
  SearchCountryField = SearchCountryField;
  CountryISO = CountryISO;
  preferredCountries: CountryISO[] = [CountryISO.Philippines, CountryISO.India];
  message: any = null;

  isEdit:boolean = false;

  selectedCountryISO: any = "ph"
  
  constructor(
    private router:Router,
    private activateRouter: ActivatedRoute,
    private apartmentService:ApartmentService,
    private visitorService:VisitorService,
    private lookupService: LookupService,
    private sessionService:SessionService,
    private staffService: StaffService,
    private sharedService: SharedService
  ) {
  
  }

  isAdmin() {
    return this.sessionService.isAdmin()
  }

  getSelectedCategory(event){
    this.visitor.visitorCategoryId = event[0].lookupValueId
  }

  getSelectedVisitType(event){
    this.visitor.visitTypeId = event[0].lookupValueId;
    this.visitor.apartmentUnitId = null;
    this.visitor.tomeetStaffId = null;
    this.block = {
      blockId:null,
      primaryName:''
    }
  }

  getSelectedBlock(event) {
    this.block.blockId = event[0].block_Id
    this.getUnits('change');
  }

  getSelectedBlockUnit(event){
    this.visitor.apartmentUnitId = event[0].buId
    this.getPrimaryName();
  }

  getSelectedStaff(event){
    this.visitor.tomeetStaffId = event[0].staffId;
  }

  getBlockId() {
    let unitDetails = {
      apartmentBlockUnitId: this.visitor.apartmentUnitId,
    };
    return this.apartmentService.getApartmentBlockUnitById(unitDetails);
  }

  async getUnits(type) {
    //Async await function integrated for checkin and checkout mode
    if(type == 'change') {
      this.visitor.apartmentUnitId = null;
      this.block.primaryName = '';
    } else {
      let tower:any = await this.getBlockId().toPromise();
      if(tower.length > 0)
        this.block.blockId = tower[0].apartmentBlockId;
    }
    for(let data of this.towerList) {
      if(this.block.blockId == data.block_Id) {
        this.unitList = data.blockUnit;
        if(type != 'change') {
          this.getPrimaryName();
          this.isDataLoaded = true;
        }
        break;
      }
    }
  }

  getPrimaryName() {
    for(let data of this.unitList) {
      if(this.visitor.apartmentUnitId == data.buId) {
        this.block.primaryName = data.il_Label;
        break;
      }
    }
  }

  expectedDurationChange() {
    if(this.visitor.expectedVisitorInTime && this.visitor.expectedVisitorOutTime) {
      let inDate = moment(this.visitor.expectedVisitorInTime);
      let outDate = moment(this.visitor.expectedVisitorOutTime);
      let diffDuration = moment.duration(outDate.diff(inDate));
      this.expectedDurationInfo = diffDuration.days()+" days "+this.isSingleDigit(diffDuration.hours())+":"+ this.isSingleDigit(diffDuration.minutes())+" hours";
      let hours = diffDuration.asHours();
      this.visitor.expectedDuration = Math.round(hours);
    } else 
        this.visitor.expectedVisitorOutTime = '';
  }

  isSingleDigit(digit){
    if(digit <= 0){
      return '00';
    } else {
      return (digit.toString().length > 1) ? digit : '0'+digit;
    }
  }

  resetField() {
    this.visitor={};
    this.visitor.visitTypeId = null;
    this.visitor.visitorCategoryId = null;
    this.block = {
      blockId:null,
      primaryName:''
    }
    this.expectedDurationInfo='';
  }

  submitExpectedVisitorForm(form) {
    
    this.message = null;
    
    if(!form.valid){
      window.scroll({
        top: 0,
        behavior: 'smooth'
      });
      // Show the validation message
      this.message = {
        appearance: 'outline',
        content   : "Fill the Required Fields",
        shake     : true,
        showIcon  : true,
        type      : 'error'
      };

    }
    else {

      this.isDataLoaded = false;

      if(!this.isEdit) {

        let params = {
          expectedVisitor : {
            ...this.visitor,
            "apartmentUnitId": this.visitor.apartmentUnitId,
            "apartmentId" : this.sessionService.apartmentId,
            "expectedVisitorPhone": this.visitor.expectedVisitorPhone.number,
            "isActive": true,
            "insertedBy": this.sessionService.userId,
            "insertedOn": _moment().toISOString(),
            "updatedBy": null,
            "updatedOn": null,
            "enteredBy": parseInt(this.sessionService.userId),
            "meetingPersonId": 1,
            "visitorsOrg": "string",
            "entryGateId": null,
            "exitGateId": null,
            "tomeetStaffId": this.visitor.tomeetStaffId,
            "phonecountrycode":  this.visitor.expectedVisitorPhone.countryCode,
            "isDailyvendor": false,
            "isFrequentvisitor": false,
          }
        }
        this.visitorService.addExpectedVisitor(params).subscribe((res:any) => {
          this.isDataLoaded = true;
          if(res.message) {
            let id = res.message.split('#');
            this.router.navigate(['confirm-expected-visitor', id[0], 'expected'], { relativeTo: this.activateRouter.parent });
          } else {
            this.sharedService.openSnackBar(res.errorMessage,'error');
          }
        })
  
      }
      else {
  
        let visitor = {
          expectedVisitor : {...this.visitor,
            "expectedVisitorPhone": this.visitor.expectedVisitorPhone.number,
            "phonecountrycode": this.visitor.expectedVisitorPhone.countryCode,
          }
        }
        this.visitorService.updateExpectedVisitor(visitor).subscribe((res:any) => {
          this.isDataLoaded = true;
          if(res.message) {
            this.router.navigate(['confirm-expected-visitor', this.activateRouter.params['value'].id, 'expected'], { relativeTo: this.activateRouter.parent });
          } else {
            this.sharedService.openSnackBar(res.errorMessage,'error');
          }
        })
  
      }

    }
    

  }

  getTowers() {
    let tower = {
     apartmentId: this.sessionService.apartmentId
    }
    return this.apartmentService.getApartmentBlockAndBlockUnitIsLivingByApartmentId(tower)
  }
  
  ngOnInit() {
    
    //set apartmentBlockUnitId and VisitorTypeId in User Mode
    if(!this.isAdmin()) {
      this.visitor.apartmentUnitId = this.sessionService.apartmentBlockUnitID;
      this.visitor.visitTypeId = 50 // unitVisitor Type
    }

    //for edit visitor
    if(this.activateRouter.params['value'].id != undefined) {
      this.isEdit = true;
      let visitor = {
        expectedVisitorId : this.activateRouter.params['value'].id
      }
      this.visitorService.getExpectedVisitorById(visitor).subscribe((res: any) => {
        this.visitor = res[0];
        this.visitor.expectedVisitorPhone = {
          'countryCode' : this.visitor.phonecountrycode,
          'number' : res[0].expectedVisitorPhone
        }
        this.expectedDurationChange();
        if(this.visitor.apartmentUnitId && this.isAdmin()) {
          this.getTowers().subscribe((res:any)=>{
            if(res.length > 0) {
              this.towerList = res;
              this.getUnits('edit');
            }
          })
        }
        else {
          this.isDataLoaded = true;
        }
      })
    }
    else {
      this.isEdit = false;
    }

     //Visit Category
     let visitCategoryParams = {
      ApartmentId: this.sessionService.apartmentId,
      LookupTypeId: 100
    }
    this.lookupService.getLookupValueByLookupTypeId(visitCategoryParams).subscribe((res:any) => {
      this.visitCategoryList = res
    })
    
    
    if(this.isAdmin()) {
      this.resetField();
      //visit type
      let visitParams = {
        ApartmentId: this.sessionService.apartmentId,
        LookupTypeId: 15
      }
      this.lookupService.getLookupValueByLookupTypeId(visitParams).subscribe((res:any) => {
        this.visitTypeList = res;
      });
      
      //Tower List 
      this.getTowers().subscribe((res:any)=>{
        if(res.length > 0) {
          this.towerList = res;
        }
      })

      //Staffs List
      let staffParms = {
        apartmentId: this.sessionService.apartmentId,
        RoleTypeId: this.sessionService.roleTypeId
      }
      this.staffService.getAllStaffs(staffParms).subscribe((res:any) => {
        this.staffsList = res;;
      });
    }

  }
}

