import { Component, OnInit, ViewChild } from '@angular/core';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { MatDialog } from '@angular/material/dialog';
import * as _ from 'underscore';
import * as moment from 'moment';
import _moment from "moment-timezone";
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { Router, ActivatedRoute } from '@angular/router';
import { SessionService } from 'src/app/core/session/session.service';

@Component({
  selector: 'app-visitor-history',
  templateUrl: './visitor-history.component.html',
  styleUrls: ['./visitor-history.component.scss']
})

export class VisitorHistoryComponent implements OnInit {
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  columnData:any;
  visitorList:any = [];
  visitorSearch:string = '';
  isVisitorDataLoaded:boolean;
  totalItems:number;

  constructor(
    private router: Router,
    private visitorService:VisitorService,
    public dialog: MatDialog,
    private sessionService:SessionService,
    private activateRouter: ActivatedRoute
  ) { 
  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'Visitor History');
  }

  navigateToCreate() {
    this.router.navigate([`checkin`],{relativeTo:this.activateRouter.parent});
  }

  onGlSearchFilter(event) {
    if(event != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = event;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }

  getCheckOutHistoryList() {
    this.isVisitorDataLoaded = false;
    let apartmentParams = {
      apartmentId: this.sessionService.apartmentId,
    }
  	this.visitorService.getVisitorsHistoryByApartmentId(apartmentParams).subscribe((res:any) => {
      if(res.length > 0) {
        let tableData = {
          localdata: res.reverse(),
          datatype: "array"
        }
        this.totalItems = tableData.localdata.length;
        this.visitorList = new jqx.dataAdapter(tableData);
      }
      this.isVisitorDataLoaded = true;
	  });
  }

  ngOnInit() {
    this.getCheckOutHistoryList();
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.columnData = [{
      text: 'Id',
      datafield: 'serialNo',
      width: 80,
      pinned: true,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },{
      text: 'Visitor Name',
      datafield: 'visitorName',
      width: 140,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },{
      text: 'Phone Number',
      datafield: 'visitorPhone',
      cellsrenderer: cellsrenderer,
      minwidth: 100,
      renderer: columnrenderer
    },{
      text: 'IN Time',
      datafield: 'visitorInTime',
      cellsrenderer: (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">'+ moment(value).format("MM/DD/YY hh:mm") +'</div>'
      },
      minwidth: 160,
      renderer: columnrenderer
    },{
      text: 'Out Time',
      datafield: 'visitorOutTime',
      cellsrenderer: (row, column, value) => {
        if(value) {
          return '<div class="jqx-custom-inner-cell">'+ moment(value).format("MM/DD/YY hh:mm") +'</div>'
        } else {
          return '<div class="jqx-custom-inner-cell"></div>'
        }
      },
      minwidth: 160,
      renderer: columnrenderer
    },{
      text: 'Visit Type',
      datafield: 'visitTypeName',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Tower Unit',
      datafield: 'block_Unit',
      cellsrenderer: cellsrenderer,
      minwidth: 170,
      renderer: columnrenderer 
    },{
      text: 'Visitor category',
      datafield: 'visitorCategoryName',
      cellsrenderer: cellsrenderer,
      minwidth: 150,
      renderer: columnrenderer
    },{
      text: 'Type',
      datafield: 'isDailyvendor',
      cellsalign: 'center', 
      align: 'center',
      cellsrenderer: (row, column, value) => {
        let label, status, isVendor, frequentVisitor, expectedVisitorId;
        isVendor = value;
        frequentVisitor = this.visitorList.loadedData[row].isFrequentvisitor;
        expectedVisitorId = this.visitorList.loadedData[row].expectedVisitorId;
        if(isVendor) {
          label = 'Vendor Pass';
          status = 'orange';
        } else if (frequentVisitor) {
          label = 'Frequent Visitor';
          status = 'green';
        } else if (!isVendor && !frequentVisitor && expectedVisitorId) {
          label = 'Expected Visitor';
          status = 'purple';
        } else {
          return `<div class="jqx-custom-inner-cell"></div>`
        }
        return `<div class="jqx-custom-inner-cell">
           <div class="status-badge bg-status-${status}-700">
             <span class="font-bold text-status-${status}-900 text-uppercase">${label}</span>
           </div>
        </div>`
      },
      minwidth: 160,
      renderer: columnrenderer,
    },];
  }
}

