import { Component, OnInit, Injector, HostListener, ViewChild, ViewEncapsulation } from '@angular/core';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { ModalService } from 'src/app/shared/services/modal.service';
import * as moment from 'moment';
import _moment from "moment-timezone";
import { MatDialog } from '@angular/material/dialog';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { Router, ActivatedRoute } from '@angular/router';
import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-user-visitor-list',
  templateUrl: './user-visitor-list.component.html',
  styleUrls: ['./user-visitor-list.component.scss']
})
export class UserVisitorListComponent implements OnInit {
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent; 
  columnData:any;
  visitorList:any = [];
  visitorSearch:string = '';
  isVisitorDataLoaded:boolean;
  totalItems:number;
  modalService:any;
  urlType:string;
  apiSubscribe:Subscription;

  constructor(
    private router: Router,
    private injector: Injector,
    private visitorService:VisitorService,
    private sessionService:SessionService,
    public dialog: MatDialog,
    private sharedService: SharedService,
    private activeRouter: ActivatedRoute,
  ) { 
    this.modalService = this.injector.get(ModalService);
    this.activeRouter.url.subscribe((data)=>{
      this.urlType = data[0].path;
    })
  }

  isAdmin() {
    return this.sessionService.isAdmin();
  }

  navigateToCreate(){
    if(this.isAdmin())   
      this.router.navigate(['/ams/visitor/info/create-expected-visitor']);
    else 
      this.router.navigate(['/user/visitor/create-expected-visitor']);
  }

  getPrintParams(event) {
    this.datagrid.exportdata(event, 'expectedVisitorData');
  }

  onGlSearchFilter(event) {
    if(event != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = event;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }

  getVisitorCheckedIn() {
    this.isVisitorDataLoaded = false;
    let param = {
      apartmentId: this.sessionService.apartmentId,
      BlockUnitId:  this.sessionService.apartmentBlockUnitID,
    }
    this.visitorService.getYetToCheckoutVisitorsByApartmentId(param).subscribe((res:any) => {
      if(res.length > 0) {
        let tableData = {
          localdata: res.reverse(),
          datatype: "array"
        }
        this.totalItems = tableData.localdata.length;
        this.visitorList = new jqx.dataAdapter(tableData);
      }
      this.isVisitorDataLoaded = true;
    },(error)=>{
      this.isVisitorDataLoaded = true;
    })
  }

  getVisitorHistory() {
    this.isVisitorDataLoaded = false;
    let param = {
      ApartmentID: this.sessionService.apartmentId,
    }
    this.visitorService.getReportsForVisitorsCheckedInMultiFilter(param).subscribe((res:any) => {
      if(res.length > 0) {
        let tableData = {
          localdata: res.reverse(),
          datatype: "array"
        }
        this.totalItems = tableData.localdata.length;
        this.visitorList = new jqx.dataAdapter(tableData);
      }
      this.isVisitorDataLoaded = true;
    },(error)=>{
      this.isVisitorDataLoaded = true;
    })
  }

  ngOnInit() {
    if(this.urlType == 'expected-visitor-user-checked-in')
      this.getVisitorCheckedIn();
    else if(this.urlType == 'expected-visitor-user-history')
      this.getVisitorHistory();
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.columnData = [{
      text: 'Id',
      datafield: 'serialNo',
      width: 80,
      pinned: true,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },{
      text: 'Visitor Name',
      datafield: 'visitorName',
      minwidth: 200,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },{
      text: 'Phone Number',
      datafield: 'visitorPhone',
      cellsrenderer: cellsrenderer,
      width: 220,
      renderer: columnrenderer
    },{
      text: 'Visitor In Time',
      datafield: 'visitorInTime',
      cellsrenderer: (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">'+ moment(value).format("MM/DD/YY hh:mm") +'</div>'
      },
      width: 240,
      renderer: columnrenderer
    },{
      text: 'Visitor Out Time',
      datafield: 'visitorOutTime',
      cellsrenderer: (row, column, value) => {
        if(value) {
          return '<div class="jqx-custom-inner-cell">'+ moment(value).format("MM/DD/YY hh:mm") +'</div>'
        } else {
          return '<div class="jqx-custom-inner-cell"></div>'
        }
      },
      width: 240,
      renderer: columnrenderer
    },{
      text: 'Visitor category',
      datafield: 'visitorCategoryName',
      cellsrenderer: cellsrenderer,
      width: 240,
      renderer: columnrenderer
    }];
    //delete expected visitor
    this.apiSubscribe = this.sharedService.unitlistdeleteindexcast.subscribe( item => {
			if(item != null && item.id ) {
        let params = {
          expectedVisitorId: item.id,
          deleteBy: this.sessionService.userId
        }
        this.visitorService.deleteExpectedVisitorById(params).subscribe((res: any) => {
          if(res.message) {
            this.sharedService.setUnitListDeleteIndex(null);
            this.datagrid.deleterow(item.index);
            this.datagrid.refresh();
            this.sharedService.openSnackBar(res.message,'success');
          } else {
            this.sharedService.openSnackBar(res.errorMessage,'error');
          }
        }, error => {
          console.log(error);
        });
			}
		});
  }

  ngOnDestroy(){
		this.apiSubscribe.unsubscribe();
  }
}

