import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CondoAnimations } from 'src/@condo/animations';
import { NgxQrcodeElementTypes, NgxQrcodeErrorCorrectionLevels } from '@techiediaries/ngx-qrcode';

import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-exp-visitor-confirm',
  templateUrl: './exp-visitor-confirm.component.html',
  styleUrls: ['./exp-visitor-confirm.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : CondoAnimations
})
export class ExpVisitorConfirmComponent implements OnInit {

  isDataLoaded:Boolean = false;

  message: any = null;

  visitor:any;

  timeZone:any;

  visitorStructure:any = {};

  elementType = NgxQrcodeElementTypes.URL;
  correctionLevel = NgxQrcodeErrorCorrectionLevels.HIGH;

  constructor(
    private router:Router,
    private activateRoute: ActivatedRoute,
    private visitorService:VisitorService,
    private sharedService: SharedService,
    private sessionService:SessionService
  ) { }


  getDateTime(date) {
    return moment(date).format(this.timeZone.time)
  }

  ngOnInit(): void {

    this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )


    // Show the validation message

    let params = {
      expectedVisitorId: this.activateRoute.params['value'].id
    }

    this.visitorService.getExpectedVisitorById(params).subscribe((res:any) => {

      this.isDataLoaded = true;

      this.visitor = res[0]

      let type = this.activateRoute.params['value'].type;

      if(type == 'frequent' && this.sessionService.isAdmin()) {
        this.visitorStructure = {
          name : 'Frequent Visitor',
          createBtnName : 'Create another frequent visitor',
          createBtnUrl : '/ams/visitor/info/frequent-visitor-create',
          viewBtnName : 'View list of frequent visitor',
          viewBtnUrl : '/ams/visitor/info/frequent-visitor-list' 
        }
      } else if (type == 'frequent' && !this.sessionService.isAdmin()) {
        this.visitorStructure = {
          name : 'Frequent Visitor',
          createBtnName : 'Create another frequent visitor',
          createBtnUrl : '/user/visitor/frequent-visitor-create',
          viewBtnName : 'View list of frequent visitor',
          viewBtnUrl : '/user/visitor/frequent-visitor-list' 
        }
      } else if (type == 'vendor') {
        this.visitorStructure = {
          name : 'Vendor Pass',
          createBtnName : 'Create another vendor pass',
          createBtnUrl : '/ams/visitor/info/vendor-pass-create',
          viewBtnName : 'View list of vendor pass',
          viewBtnUrl : '/ams/visitor/info/vendor-pass-list' 
        }

      } else if (type == 'expected' && this.sessionService.isAdmin()) {
        this.visitorStructure = {
          name : 'Expected Visitor',
          createBtnName : 'Create another expected visitor',
          createBtnUrl : '/ams/visitor/info/create-expected-visitor',
          viewBtnName : 'View list of expected visitor',
          viewBtnUrl : '/ams/visitor/info/expected-visitor-list' 
        }
      } else if (type == 'expected' && !this.sessionService.isAdmin()) {
        this.visitorStructure = {
          name : 'Expected Visitor',
          createBtnName : 'Create another expected visitor',
          createBtnUrl : '/user/visitor/create-expected-visitor',
          viewBtnName : 'View list of expected visitor',
          viewBtnUrl : '/user/visitor/expected-visitor-user-list' 
        }
      }

      this.message = {
        appearance: 'outline',
        content   : `${this.visitorStructure.name} created. Please share the below details to your visitor`,
        shake     : false,
        showIcon  : true,
        type      : 'success'
      };

    }, error => {
      this.isDataLoaded = true;
      this.sharedService.openSnackBar('Server Error','error');
    })

  }

}
