import { Component, OnInit, Injector, ViewChild, ViewEncapsulation } from '@angular/core';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { ModalService } from 'src/app/shared/services/modal.service';
import { MatDialog } from '@angular/material/dialog';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { Router, ActivatedRoute } from '@angular/router';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';

import * as moment from 'moment';
import _moment from "moment-timezone";
import { Subscription } from 'rxjs';
import { ConfirmDialogModel, CommonConfirmModalComponent } from 'src/app/shared/components/common-confirm-modal/common-confirm-modal.component';

@Component({
  selector: 'app-frequent-vendor-list',
  templateUrl: './frequent-vendor-list.component.html',
  styleUrls: ['./frequent-vendor-list.component.scss'],
  encapsulation: ViewEncapsulation.None
})

export class FrequentVendorListComponent implements OnInit {
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent; 
  visitorList:any = [];
  visitorSearch:string = '';
  isVisitorDataLoaded:boolean;
  modalService:any;
  urlType:string;
  apiSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any = 0;
  itemLimit: any = 10;

  timeZone:any;

  constructor(
    private router: Router,
    private injector: Injector,
    private visitorService:VisitorService,
    private sessionService:SessionService,
    public dialog: MatDialog,
    private sharedService: SharedService,
    private activeRouter: ActivatedRoute,
  ) { 
    this.modalService = this.injector.get(ModalService);
    this.activeRouter.url.subscribe((data)=>{
      this.urlType = data[0].path;
    })
  }

  get isAdmin() {
    return this.sessionService.isAdmin();
  }

  navigateToCreate(){
    if(this.urlType == 'frequent-visitor-list') 
     this.router.navigate(['frequent-visitor-create'],{relativeTo:this.activeRouter.parent});
    else if(this.urlType == 'vendor-pass-list') 
      this.router.navigate(['vendor-pass-create'],{relativeTo:this.activeRouter.parent});
  }

  getDateTime(date) {
    return moment(date).format('hh:mm A')
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  checkIn(item) {
    const message = `Are you sure you want to Check In?`;
    const dialogData = new ConfirmDialogModel("Confirm Action", message);
    const dialogRef = this.dialog.open(CommonConfirmModalComponent, {
      panelClass: 'material-dialog-medium',
      disableClose: true,
      data: dialogData
    });
    dialogRef.afterClosed().subscribe(dialogResult => {
      if (dialogResult) {
        let pageAccess:string;
        if(this.urlType == 'frequent-visitor-list') 
          pageAccess = 'frequent';
        else if(this.urlType == 'vendor-pass-list') 
          pageAccess = 'vendor';
        this.router.navigate([`checkin/${item.expectedVisitorId}`],{relativeTo:this.activeRouter.parent,queryParams : {access : pageAccess}});
      }
    });
  }

  editVisitor(expectedVisitorId){
    this.router.navigate([`/user/visitor/expected-visitor/edit/${expectedVisitorId}`]);
  }

  deleteVisitor(id, index){
    this.modalService.showConfirmModal({id:id,index:index});
  }

  getVendorPassList() {
    this.isVisitorDataLoaded = false;
    let param = {
      apartmentId: this.sessionService.apartmentId,
      fromDate: '',
      toDate: '',
    }
    return this.visitorService.getAllDvYetToComeExpectedVisitorsByApartmentIdDate(param);
  }

  getFrequentVisitorList() {
    this.isVisitorDataLoaded = false;
    let param = {
      apartmentId: this.sessionService.apartmentId,
      fromDate: '',
      toDate: '',
    }
    return this.visitorService.getAllFvYetToComeExpectedVisitorsByApartmentIdDate(param)
  }

  getVisitorList() {
    let visitorList;
    if(this.urlType == 'frequent-visitor-list') 
      visitorList = this.getFrequentVisitorList();
    else if(this.urlType == 'vendor-pass-list') 
      visitorList = this.getVendorPassList();
     
    visitorList.subscribe((res:any) => {
      if(res.length > 0) {
        this.visitorList = res.reverse();
        this.totalItems = this.visitorList.length;
        if (this.totalItems > this.itemLimit) {
          this.ItemEndIndex = this.itemLimit;
        }
        else {
          this.ItemEndIndex = this.totalItems;
        }
        this.isVisitorDataLoaded = true;
      }
      else {
        this.isVisitorDataLoaded = true;
      }
    },(error)=>{
      this.isVisitorDataLoaded = true;
      this.sharedService.openSnackBar('Server Error','error');
    })
  }

  ngOnInit() {
    this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone );

    this.getVisitorList();
    
    //delete expected visitor
    this.apiSubscribe = this.sharedService.unitlistdeleteindexcast.subscribe( item => {
			if(item != null && item.id ) {
        let params = {
          expectedVisitorId: item.id,
          deleteBy: this.sessionService.userId
        }
        this.visitorService.deleteExpectedVisitorById(params).subscribe((res: any) => {
          if(res.message) {
            this.sharedService.setUnitListDeleteIndex(null);
            
            this.visitorList = this.visitorList.filter( visitor => {
              return visitor.expectedVisitorId != item.id
            })

            this.totalItems = this.visitorList.length;

            if (this.totalItems > this.itemLimit) {
              this.ItemEndIndex = this.itemLimit;
            }
            else {
              this.ItemEndIndex = this.totalItems;
            }

            this.sharedService.openSnackBar(res.message,'success');
          } else {
            this.sharedService.openSnackBar(res.errorMessage,'error');
          }
        }, error => {
          this.sharedService.openSnackBar('Server Error','error');
        });
			}
		});
  }

  ngOnDestroy(){
		this.apiSubscribe.unsubscribe();
  }
}
