import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { VisitorSetupComponent } from './components/visitor-setup/visitor-setup.component';
import { VisitorCreateCheckinComponent } from './components/visitor-create-checkin/visitor-create-checkin.component';
import { ExpVisitorCreateChekinComponent } from './components/exp-visitor-create-chekin/exp-visitor-create-chekin.component';
import { ExpVisitorListComponent } from './components/exp-visitor-list/exp-visitor-list.component';
import { UserVisitorListComponent } from './components/user-visitor-list/user-visitor-list.component';
import { ExpUserVisitorListComponent } from './components/exp-user-visitor-list/exp-user-visitor-list.component';
import { CreateVendorFrquentVisitorComponent } from './components/create-vendor-frquent-visitor/create-vendor-frquent-visitor.component';
import { ExpVisitorConfirmComponent } from './components/exp-visitor-confirm/exp-visitor-confirm.component';
import { FrequentVendorListComponent } from './components/frequent-vendor-list/frequent-vendor-list.component';
import { CurrentlyCheckedinListComponent } from './components/currently-checkedin-list/currently-checkedin-list.component';
import { VisitorHistoryComponent } from './components/visitor-history/visitor-history.component';

const routes: Routes = [
	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'settings', component: VisitorSetupComponent, canActivate: [AuthGuard] },
	{ path: 'checkin', component: VisitorCreateCheckinComponent, canActivate: [AuthGuard] },
	{ path: ':type/:id', component: VisitorCreateCheckinComponent, canActivate: [AuthGuard] },
	{ path: 'expected-visitor-list', component: ExpVisitorListComponent, canActivate: [AuthGuard] },
	{ path: 'create-expected-visitor', component: ExpVisitorCreateChekinComponent, canActivate: [AuthGuard]  },
	{ path: 'confirm-expected-visitor/:id/:type', component: ExpVisitorConfirmComponent, canActivate: [AuthGuard]  },
	{ path: 'expected-visitor/:type/:id', component: ExpVisitorCreateChekinComponent, canActivate: [AuthGuard]  },
	{ path: 'checkout', component: CurrentlyCheckedinListComponent, canActivate: [AuthGuard] },
	{ path: 'history', component: VisitorHistoryComponent, canActivate: [AuthGuard]  },
	{ path: 'expected-visitor-user-checked-in', component: UserVisitorListComponent, canActivate: [AuthGuard]  },
	{ path: 'expected-visitor-user-history', component: UserVisitorListComponent, canActivate: [AuthGuard]  },
	{ path: 'expected-visitor-user-list', component: ExpUserVisitorListComponent, canActivate: [AuthGuard]  },
	{ path: 'frequent-visitor-list', component: FrequentVendorListComponent, canActivate: [AuthGuard]  },
	{ path: 'frequent-visitor-create', component: CreateVendorFrquentVisitorComponent, canActivate: [AuthGuard]  },
	{ path: 'vendor-pass-list', component: FrequentVendorListComponent, canActivate: [AuthGuard]  },
	{ path: 'vendor-pass-create', component: CreateVendorFrquentVisitorComponent, canActivate: [AuthGuard]  },

	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class VisitorRoutingModule { }
