import { Component, OnInit, Injector, ViewEncapsulation } from '@angular/core';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { ModalService } from 'src/app/shared/services/modal.service';
import { MatDialog } from '@angular/material/dialog';
import { Router, ActivatedRoute } from '@angular/router';
import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { Subscription } from 'rxjs';

import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-exp-user-visitor-list',
  templateUrl: './exp-user-visitor-list.component.html',
  styleUrls: ['./exp-user-visitor-list.component.scss'],
  encapsulation: ViewEncapsulation.None
})

export class ExpUserVisitorListComponent implements OnInit {
  
  visitorList:any = [];
  visitorSearch:string = '';
  isVisitorDataLoaded:boolean;
  modalService:any;
  apiSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 10;

  timeZone:any;

  constructor(
    private router: Router,
    private injector: Injector,
    private visitorService:VisitorService,
    private sessionService:SessionService,
    public dialog: MatDialog,
    private sharedService: SharedService,
    private activeRouter: ActivatedRoute,
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  navigateToCreate(){
    this.router.navigate(['create-expected-visitor'], { relativeTo: this.activeRouter.parent });
  }

  getDate(date) {
    return moment(date).format(this.timeZone.date)
  }

  getDateTime(date) {
    return moment(date).format(this.timeZone.time)
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  editVisitor(expectedVisitorId){
    this.router.navigate([`expected-visitor/edit/${expectedVisitorId}`], { relativeTo: this.activeRouter.parent });
  }

  deleteVisitor(id, index){
    this.modalService.showConfirmModal({id:id,index:index});
  }


  getVisitorList() {
    
    this.isVisitorDataLoaded = false;
    
    let param = {
      apartmentUnitId: this.sessionService.apartmentBlockUnitID
    }

    this.visitorService.getExpectedVisitorsByApartmentUnitId(param).subscribe((res:any) => {
      
      if(res.length > 0) {
        
        this.visitorList = res;
        this.totalItems = this.visitorList.length;

        if (this.totalItems > this.itemLimit) {
          this.ItemEndIndex = this.itemLimit;
        }
        else {
          this.ItemEndIndex = this.totalItems;
        }

        this.isVisitorDataLoaded = true;
      }
      else {
        this.isVisitorDataLoaded = true;
      }
    },(error)=>{
      this.isVisitorDataLoaded = true;
    })
  }

  ngOnInit() {

    this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )

    this.getVisitorList();

    
    //delete expected visitor
    this.apiSubscribe = this.sharedService.unitlistdeleteindexcast.subscribe( item => {
			if(item != null && item.id ) {
        let params = {
          expectedVisitorId: item.id,
          deleteBy: this.sessionService.userId
        }
        this.visitorService.deleteExpectedVisitorById(params).subscribe((res: any) => {
          if(res.message) {
            this.sharedService.setUnitListDeleteIndex(null);
            
            this.visitorList = this.visitorList.filter( visitor => {
              return visitor.expectedVisitorId != item.id
            })

            this.totalItems = this.visitorList.length;

            if (this.totalItems > this.itemLimit) {
              this.ItemEndIndex = this.itemLimit;
            }
            else {
              this.ItemEndIndex = this.totalItems;
            }

            this.sharedService.openSnackBar(res.message,'success');
          } else {
            this.sharedService.openSnackBar(res.errorMessage,'error');
          }
        }, error => {
          this.sharedService.openSnackBar('Server Error','error');
        });
			}
		});
  }

  ngOnDestroy(){
		this.apiSubscribe.unsubscribe();
  }
}

